.navbar {
  display: block;
  flex-direction: column;
  padding: 20px;
  background-color: #f6941c17; /* Switch to a clean white background */
  border-radius: 12px; /* Softer round corners */
  box-shadow: 0 10px 30px rgba(0,0,0,0.05); /* Softer shadow for depth */
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'San Francisco', 'Helvetica Neue', sans-serif; /* Modern font */
  align-items: flex-start; /* Align items to the start for a clean look */
  gap: 20px; /* Use gap to provide consistent spacing between items */
  height:fit-content;
  z-index: 900;
  position: sticky;
  top: 70px;
}

