}

/* For additional hover effect */
.settings-header:hover {
  box-shadow: 0px 1px 0px 0px #a4a4a4; /* Darker shadow on hover for the line */
  /* Or for the pseudo-element approach */
  /* border-color for :after pseudo-element */
}

.conditional-settings {
  margin-top: 10px;
}

/* Custom Slider Styles */
.settings-group input[type=range] {
  -webkit-appearance: none; /* Override default appearance */
  appearance: none;
  width: 100%; /* Full-width */
  height: 10px; /* Specifying height for the track */
  border-radius: 5px; /* Rounded corners for the track */
  background: #e0e0e0; /* Light grey background */
  outline: none; /* Remove the outline */
  opacity: 0.7; /* Slightly transparent track */
  transition: opacity 0.2s, box-shadow 0.2s; /* Smooth transitions for hover and focus */
  margin-bottom: 10px;
}

/* Track styles for different browsers */
.settings-group input[type=range]::-webkit-slider-runnable-track {
  height: 10px;
  border-radius: 5px;
  background: linear-gradient(to right, #0ae, #58f); /* Gradient fill for the track */
}

.settings-group input[type=range]::-moz-range-track {
  height: 10px;
  border-radius: 5px;
  background: linear-gradient(to right, #0ae, #58f); /* Gradient fill for the track */
}

.settings-group input[type=range]::-ms-track {
  height: 10px;
  border-radius: 5px;
  background: linear-gradient(to right, #0ae, #58f); /* Gradient fill for the track */
  border-color: transparent;
  color: transparent;
}

/* Thumb Styles */
.settings-group input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: -5px; /* Align thumb with the track */
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  border: 3px solid #58f; /* Border color should match the fill color */
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2); /* Subtle shadow to lift the thumb */
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth transition for pressing the thumb */
}

.settings-group input[type=range]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  border: 3px solid #58f;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

.settings-group input[type=range]::-ms-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #fff;
  border: 3px solid #58f;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

/* Interaction States: Hover, Active, and Focus */
.settings-group input[type=range]:hover,
.settings-group input[type=range]:active,
.settings-group input[type=range]:focus {
  opacity: 1; /* Fully opaque on interaction */
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.5); /* Blue glow for focus */
}

.settings-group input[type=range]:active::-webkit-slider-thumb,
.settings-group input[type=range]:focus::-webkit-slider-thumb {
  transform: scale(1.1); /* Scale up the thumb when pressed */
  box-shadow: 0 0 12px rgba(0, 123, 255, 0.5); /* More pronounced shadow for active state */
}

.settings-group input[type=range]:active::-moz-range-thumb,
.settings-group input[type=range]:focus::-moz-range-thumb {
  transform: scale(1.1);
  box-shadow: 0 0 12px rgba(0, 123, 255, 0.5);
}

.settings-group input[type=range]:active::-ms-thumb,
.settings-group input[type=range]:focus::-ms-thumb {
  transform: scale(1.1);
  box-shadow: 0 0 12px rgba(0, 123, 255, 0.5);
}

/* Professional Toggle Switch Styles */
.switch {
  position: relative;
  display: inline-block;
  width: 55px; /* Width of the switch */
  height: 28px; /* Height of the switch */
  margin-left: 1px;
  margin-bottom: 10px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* Slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc; /* Default background color */
  transition: .4s; /* Animation transition time */
  border-radius: 34px; /* Circular edges */
  box-shadow: inset 0 2px 3px rgba(0,0,0,0.2); /* Inner shadow for depth */
}

/* Slider Knob */
.slider:before {
  position: absolute;
  content: "";
  height: 20px; /* Slightly smaller than the container for margins */
  width: 20px; /* Width of the slider knob */
  left: 4px; /* Small left margin from the container */
  bottom: 4px; /* Small bottom margin from the container */
  top: 4px;
  background-color: white; /* Color of the slider knob */
  transition: .4s; /* Animation transition time */
  border-radius: 50%; /* Circular knob */
  box-shadow: 0 4px 6px rgba(0,0,0,0.1); /* Outer shadow for 3D effect */
}

/* Checked State */
input:checked + .slider {
  background-color: #66bb6a; /* Active state background color */
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3; /* Glow effect for focus state */
}

input:checked + .slider:before {
  transform: translateX(26px); /* Slide the knob to the right */
}

/* Hover effects on the slider */
.slider:hover {
  background-color: #b3d4fc; /* Light glow when hovering over the slider (for off state) */
}

input:checked + .slider:hover {
  background-color: #8bc34a; /* Light glow when hovering over the slider (for on state) */
}

/* Disabled State */
input:disabled + .slider {
  background-color: #e0e0e0;
  cursor: not-allowed;
}

input:disabled + .slider:before {
  background-color: #bdbdbd;
}


