.tooltip {
  position: absolute;
  background-color: #f9f9f9; /* Light grey background */
  color: #333; /* Dark grey text for contrast */
  border: 1px solid #ddd; /* Light grey border */
  border-radius: 8px; /* Rounded corners */
  padding: 15px; /* Padding inside the tooltip */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
  z-index: 100; /* Ensure it's above other content */
  max-width: 250px; /* Maximum width of the tooltip */
  word-wrap: break-word; /* Ensure long words do not overflow */
  font-family: Arial, sans-serif; /* A more neutral, readable font */
  font-size: 14px; /* Slightly smaller font for tooltip text */
  line-height: 1.5; /* Improved line spacing for readability */
}