.d3-tooltip {
  position: absolute;
  text-align: center;
  width: auto;
  height: auto;
  padding: 2px;
  font: 12px sans-serif;
  background: lightsteelblue; /* Default background, will be overridden dynamically */
  border: 0;
  border-radius: 8px;
  pointer-events: none;
  opacity: 0;
}
  
/* BarChartWithD3.css */

.line-graph {
  font-family: 'Arial', sans-serif;
}

.line-graph .axis path,
.line-graph .axis line {
  fill: none;
  stroke: #333;
  shape-rendering: crispEdges;
}

.line-graph .grid line {
  stroke: lightgrey;
  stroke-opacity: 0.5;
  shape-rendering: crispEdges;
}

.line-graph .grid path {
  stroke-width: 0;
}

.line-graph path.line {
  stroke: rgba(247, 50, 39, 0.625);
  stroke-width: 2;
  fill: none;
}
