.content {
  display: flex;
  flex: 1;
  padding: 20px;
  padding-top: 100px;
  background: linear-gradient(to bottom, rgb(235, 245, 255), #ffffff);
  transition: background 0.3s linear;
  position: absolute;
  width: 100%;
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 20px;
}

@media (min-width: 1024px) {
  .app-container {
    display: flex;
    flex-direction: column;
    /* Stack children vertically */
    align-items: center;
    /* Center children horizontally */
    width: 100%;
    /* Use the full width of the viewport */
    margin-top: 20px;
  }
}

.graph-container {
  width: 60vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  /* Consistent with card style */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* Optional for depth */
}


.konamiheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #ff4800;
  color: #ffffff;
  text-align: "center";
  position: "relative";
  animation: "fadeIn 2s";
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #ffffff;
  color: #333333;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  z-index: 1000;
  width: 100%;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
  /* Adjust the gap between logo and company name as needed */
}

.logo img {
  width: 220px;
  /* Adjust based on your logo size */
  /* margin-right: 10px; Removing this in favor of gap property */
}

.konami-gif-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1000;
}

.section-wrapper {
  border-radius: 12px;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(240, 240, 240);
}

.subsection-title {
  font-size: 18px;
  font-weight: 800;
  color: #666;
  text-align: center;
  margin-top: 0;
  margin-bottom: -18px;
  padding: 12px 1px;
  background-color: #80c6ff;
  border-radius: 2px;
}

.section-divider {
  border: none;
  height: 1px;
  background-color: #ccc;
  /* light grey line */
  margin: 20px 0;
  /* space above and below the divider */
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes dropIn {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.arco-layout-footer {
  flex: 0 0 auto;
  margin: 5px;
}

